<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />

    <van-cell :title="item.title" is-link @click="goTo(index)" v-for="(item, index) in list"/>

  </div>
</template>

<script>
  export default {
    name: "gtja",
    data(){
      return {
        title:'国泰君安',
        // page
        list: [
          {title: '利率互换', path: '/mall/gtja/interestRate'},
          {title: '信用违约互换', path: '/mall/gtja/creditDefaultSwap'},
          {title: '结构金融日报', path: '/mall/gtja/financialTimes'},
          {title: '业务人员联系方式', path: '/mall/gtja/linked'},
          {title: '免责声明', path: '/mall/gtja/declare'},
          {title: '运营管理', path: '/mall/gtja/operation'}
        ],
        hasPower: false
      }
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      },
      goTo (index) {
        if (index < 4) {
          if (this.hasPower) {

          } else {
            this.$dialog.confirm({
              message: '尚未获得国泰君安数据查询权限',
              showCancelButton: false,
              confirmButtonText: '点击申请权限',
              closeOnClickOverlay: true
            }).then(() => {
              this.$router.push('/mall/gtja/applyInformation')
            }).catch(() => {

            });
          }
        } else {
          this.$router.push(this.list[index].path)
        }
      }
    },
    created() {
      let token = sessionStorage.getItem('token');

      this.$http.post(backstageIp + userUrl + '/getFlagshipData',
        {
          url:'https://10.10.55.13/mall//user/getUserPerms.json?authorization=',
          type:'get',
          cookie: 'token=' + token
        }
      ).then(res => {
        let data = JSON.parse(res.data.data).data;
        for (let i = 0; i < data.length; i++) {
          let item = data[i];

        }
        console.log(data);

      }).catch(err => {
        console.log(err);
      });
    }
  }
</script>

<style scoped>

  .van-cell {
    font-size: 16px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(32, 32, 32, 1);
    line-height: 22.5px;
    padding: 12px 15px;
  }

</style>



